/*
black :#252A34
red :#FF2E63
white : #EAEAEA
green : #08D9D6
*/

.form-box {
  display: -ms-flexbox;
  display: flex;
  font-family: 'Oleo Script', cursive;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.form-input {
  font-family: 'Oleo Script', cursive;
  text-align: center;
  font-size: 4rem;
  color: #f8f8f8;
  height: 4rem;
  width: 33%;
  outline: none;
  border: none;
  border-bottom: 4px dashed #f8f8f8;
  background-color: #252A34
}

.overlap {
  border-bottom: 4px dashed #FF2E63;
}

.form-overlap {
  font-size: 3rem;
  color: #FF2E63;
}

.form-text {
  font-size: 3rem;
  padding-right: 2rem;
}

.form-button {
  padding-left: 2rem;
  font-size: 4rem;
  margin-left: 1rem;
  border-radius: 3px;
  color: white;
  font-weight: 100;
  cursor: pointer;
}

.form-button:hover {
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  color: #FF2E63;
}


